import React from 'react';
import PropTypes from 'prop-types';
import RcSteps from 'rc-steps';
import classnames from 'classnames';
import CheckCircleO from '@wind/icons/lib/icons/CheckCircleO';
import CloseCircleO from '@wind/icons/lib/icons/CloseCircleO';
const RCStep = RcSteps.Step;
export default class Step extends React.Component {
    constructor() {
        super(...arguments);
        this.getStatusIcon = (status) => {
            const { prefixCls } = this.props;
            let icon;
            switch (status) {
                case 'finish':
                    icon = <CheckCircleO className={`${prefixCls}-item-status-icon`}/>;
                    break;
                case 'error':
                    icon = <CloseCircleO className={`${prefixCls}-item-status-icon`}/>;
                    break;
            }
            return icon;
        };
    }
    render() {
        const { icon, status, progressDot, prefixCls, className } = this.props;
        // console.log('Step', this.props);
        const statusIcon = icon == null && !progressDot && status != null ? this.getStatusIcon(status) : undefined;
        // const realIcon = status === 'finish'
        return (<RCStep icon={statusIcon} {...this.props} className={classnames(className, {
            [`${prefixCls}-item-status`]: statusIcon != null
        })}/>);
    }
}
Step.defaultProps = {
    prefixCls: 'w-steps',
};
Step.propTypes = {
    prefixCls: PropTypes.string,
    className: PropTypes.string,
    status: PropTypes.string,
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    description: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    icon: PropTypes.oneOfType([PropTypes.bool, PropTypes.func]),
    progressDot: PropTypes.oneOfType([PropTypes.bool, PropTypes.func])
};
